body {
  margin: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #0E1014;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper{
  position: relative;
  width: 1440px;
  height: 980px;
}

.purple-ball{
  position: absolute;
  width: 60px;
  height: 60px;
  left: 868px;
  top: -2px;

  background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(236, 208, 255, 0.64) 0%, rgba(161, 93, 207, 0.24) 59.67%, rgba(118, 49, 164, 0.208) 78.59%, rgba(86, 24, 128, 0.232) 100%); 
  box-shadow: 3px 6px 27px -10px rgba(177, 118, 217, 0.25);
  filter: blur(5px);
  backdrop-filter: blur(2px);
  border-radius: 100%;
  z-index: -1;
}

.red-ball{
  position: absolute;
  width: 83px;
  height: 83px;
  left: 519px;
  top: 83px;

  background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(255, 208, 208, 0.536) 0%, rgba(212, 93, 93, 0.264) 59.67%, rgba(167, 49, 49, 0.248) 78.59%, rgba(130, 25, 25, 0.248) 100%);
  box-shadow: 3px 6px 27px -10px rgba(223, 125, 125, 0.25);
  backdrop-filter: blur(2px);
  border-radius: 100%;
  z-index: -1;
}

.red-ball-second{
  position: absolute;
  width: 28px;
  height: 28px;
  left: 851px;
  top: 547px;

  background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(255, 208, 208, 0.48) 0%, rgba(212, 93, 93, 0.248) 59.67%, rgba(167, 49, 49, 0.264) 78.59%, rgba(130, 25, 25, 0.232) 100%); 
  box-shadow: 3px 6px 27px -10px rgba(223, 125, 125, 0.25);
  filter: blur(2.5px);
  backdrop-filter: blur(2px);
  border-radius: 100%;
  z-index: -1;
}

.purple-light{
  position: absolute;
  width: 259px;
  height: 259px;
  left: 602px;
  top: -154px;

  background: #833AB4;
  opacity: 0.5;
  filter: blur(196px);
  z-index: -1;
}

.red-light{
  position: absolute;
  width: 311px;
  height: 311px;
  left: -102px;
  top: 251px;

  background: #961A1A;
  opacity: 0.5;
  filter: blur(196px);
  z-index: -1;
}

.flex{
  display: flex;
  flex-direction: column;
}

.direction-row{
  flex-direction: row;
}

.align-items-center{
  align-items: center;
}

.align-items-start{
  align-items: flex-start;
}

.align-items-end{
  align-items: flex-end;
}

.justify-between{
  justify-content: space-between;
}

.justify-end{
  justify-content: flex-end
}


